html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}.container{margin:0 auto;max-width:65%}@media(max-width: 1600px){.container{max-width:70%}}@media(max-width: 1200px){.container{max-width:75%}}@media(max-width: 992px){.container{max-width:80%}}@media(max-width: 768px){.container{max-width:90%}}@media(max-width: 576px){.container{max-width:95%}}body{margin:0;font-family:"Open Sans",sans-serif,"Oleo Script",cursive;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-image:url(data:image/png;base64,);background-color:rgba(0,0,0,.05);color:#444}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.header{display:flex;justify-content:space-between;padding:.5em 0;align-items:center}.header_logo{color:#f1453e;font-size:2em;font-family:"Oleo Script",cursive;cursor:pointer;text-decoration:none}.header_link{display:flex;grid-gap:.5em;gap:.5em;font-weight:700}.header_link li{padding:.5em;border-radius:3px;cursor:pointer;transition:all .5s ease-in-out}.header_link li a{color:#444;text-decoration:none;text-transform:capitalize}.header_link li:hover{background-color:#444}.header_link li:hover a{color:#fff}.header_link .active{background-color:#444}.header_link .active a{color:#fff}.mobile_menu_icon{display:none;transition:all .5s ease-in-out}@media screen and (max-width: 992px){.header_link{display:none}.mobile{position:absolute;display:block;list-style:none;left:0;top:3em;width:100%;text-align:center;transition:all .5s ease-in-out;background-color:rgba(255,255,255,.15);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px)}.mobile li{padding:1em}.mobile_menu_icon{display:flex;cursor:pointer;border:none;outline:none;border-radius:3px;background-color:transparent;font-size:2em;right:100%;color:#444}}
.footer{display:flex;flex-direction:column;align-items:center;justify-content:center;margin:2em auto 1em auto;grid-gap:2em;gap:2em;color:#8b8b8b}.footer_info{display:grid;grid-gap:1em;gap:1em;width:100%;grid-template-columns:repeat(auto-fit, minmax(13rem, 1fr))}.footer_aside{display:flex;flex-direction:column;grid-gap:1em;gap:1em}.footer_aside_info{display:flex;flex-direction:column;grid-gap:.5em;gap:.5em}.footer_aside_info p{cursor:pointer;text-decoration:underline;font-weight:500;transition:all .5s ease-in-out}.footer_aside_info p:hover{color:#444}.footer_aside h2{font-size:1.2em;font-weight:900;color:#444}@media(max-width: 474px){.footer_info{grid-template-columns:1fr;text-align:center}.brand-icon{justify-content:center}}.brand-icon{font-size:2em;color:#444;display:flex;grid-gap:.5em;gap:.5em;cursor:pointer;flex-wrap:wrap}.brand-icon svg{transition:all .5s ease-in-out}.brand-icon svg:hover{color:#8b8b8b}.address{display:flex;flex-direction:column;grid-gap:.5em;gap:.5em}
.button{white-space:nowrap;padding:.3em .5em;border:none;font-weight:800;display:flex;grid-gap:1em;gap:1em;align-items:center;justify-content:space-between;transition:all .5s ease-in-out;border-radius:5px;cursor:pointer}.primary{background-color:#1591c3;color:#fff}.primary:hover{background-color:#49b4df}.secundary{background-color:#f2f2f2;color:#444}.secundary:hover{background-color:#e0e0e0}
.home-banner{margin-top:1em;background-color:#fff;border-radius:5px;font-size:2em;padding:1em;display:flex;grid-gap:1em;gap:1em}.home-banner_info{display:flex;flex-direction:column;grid-gap:.5em;gap:.5em}.home-banner_info h1{font-weight:800;text-transform:capitalize}.home-banner_info p{font-size:.8em;font-weight:lighter;line-height:1.3em;color:#8b8b8b;text-align:justify}.home-banner_buttons{display:flex;flex-direction:column;grid-gap:.5em;gap:.5em}.home-banner_buttons button{font-size:1em}@media screen and (max-width: 320px){.home-banner_buttons button{font-size:.7em}}.home-banner_buttons button .animate-button-icon{transition:transform 300ms}.home-banner_buttons button svg{font-size:.7em}.home-banner_buttons button:hover .animate-button-icon{transform:translateX(0.5em)}@media screen and (max-width: 992px){.home-banner{flex-direction:column}}
.cards{display:grid;grid-gap:2em;gap:2em;grid-template-columns:repeat(auto-fit, minmax(min(100%, 15rem), 1fr));margin-top:2em}.card{height:20em;background-color:#fff;border-radius:5px;cursor:pointer;box-shadow:0px 2px 15px rgba(0,0,0,.1);transition:all .5s ease-in-out}.card_img{position:relative;height:60%}.card_img img{object-fit:cover;height:100%;width:100%;border-top-left-radius:5px;border-top-right-radius:5px}.card_img span{transition:all .5s ease-in-out;color:#fff;position:absolute;left:.5em;bottom:.5em;font-size:1.1em;font-weight:800}.card_info{padding:.5em}.card_info h3{font-weight:800;color:#585858;margin-bottom:.5em}.card_info p{color:#8b8b8b;line-height:1.5em}.card:hover{transform:scale(1.1)}.card:hover .card_img img{-webkit-filter:grayscale(100%);filter:grayscale(100%)}.card:hover .card_img span{color:#fff;font-size:1.4em}
.home-info{background-color:#fff;margin-top:2em}.home-info_content{padding:2em;display:flex;justify-content:center}.home-info_content_interesting{width:40%;margin-right:1em}.home-info_content_interesting h3{font-size:1.1em;font-weight:700}.home-info_content_interesting div{margin:1em auto 1em 0;display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:1em;gap:1em;width:80%}.home-info_content_interesting div img{object-fit:cover;width:100%;border-radius:5px}.home-info_content_interesting button{margin-top:1em;font-size:1.1em;padding:.5em 1em}.home-info_content_info{width:60%}.home-info_content_info h1{font-size:1.5em;font-weight:800}.home-info_content_info div{margin:1em 0}.home-info_content_info div p{line-height:1.8em;color:#8b8b8b;text-align:justify}.home-info_content_info div p:first-child{margin-bottom:1em}.home-info_content_info button{font-size:1.1em;padding:.5em 1em}@media(max-width: 768px){.home-info_content{flex-direction:column}.home-info_content_interesting{width:100%;margin-right:0;margin-bottom:2em;display:flex;align-items:center;flex-direction:column}.home-info_content_interesting div{margin:1em auto}.home-info_content_interesting button{margin-top:0}.home-info_content_info{display:flex;flex-direction:column;align-items:center;margin:0 auto;width:100%}}
